		import toImg from '../../components/toImg.vue'
export default {
	data() {
		return {
			details:{},
			myid:"",
			params:{
				limit:6,
				order:"new",
				skip:0,
				first:1
			},
			album:{},
			wallpaper:[],
			hastrue:true
		}
	},
	methods: {
		getdetails() {
			
			uni.showLoading({
				title:"加载中",
				mask:true
			})				
			
			uni.request({
				
				
				
				



				

				url:"http://157.122.54.189:9088/image/v1/wallpaper/album/"+this.myid+"/wallpaper",

				
				data:this.params,
				success: (result) => {

					if(result.data.res.wallpaper.length == 0){
						
						this.hastrue =false;
						uni.showToast({
							title:"数据没有了",
							icon:"none"
						})
						return;
						
					}
					if(this.params.first == 1){
						this.album = result.data.res.album;
					}
					
					this.wallpaper = [...this.wallpaper,...result.data.res.wallpaper];
					
					//console.log(result.data);
				},
				complete: () => {
					uni.hideLoading();
				}
			})
		}
	},
	onReachBottom() {
		this.params.first = 0;
		if(this.hastrue){
			this.params.skip += this.params.limit
			this.getdetails();	
			//console.log(11);
		}

		
	},
	components:{
		toImg
	},
	onLoad(option) {
		this.myid =option.id;
		this.getdetails();
	}
}
